import './PizzaBox.css';
import 'bootstrap/dist/css/bootstrap.min.css';

export default function PizzaBox({name,pic= "icons/pizza-slice.png",count , children}) {
    
    
    return (
        
        count &&
        <div className="pizzaBox">
           

            <a href="#">
                <img className='img-fluid' src={pic} alt="" />
            </a>
           
            <div className="pizzaSummary">

                <a href='#' className='pizza-title'>{name}</a>
                <span className='pizza-subtitle'>     <br />         .</span>
            </div>
            {children}
            <div className="links">
                <form>
                    <span className='pizza-size'></span>
                    <span className='pizza-weight'>300 </span>
                    <span className='pizza-price'>28 </span>
                    <span className='ordery'><a href="#"> </a></span>
                </form>
                <form>
                    <span className='pizza-size'></span>
                    <span className='pizza-weight'>490 </span>
                    <span className='pizza-price'>43 </span>
                    <span className='ordery'><a href="#"> </a></span>
                </form>
                <a href='#' className='pizza-more'>  </a>

            </div>
        </div>



    )
}

